:root{--color-primary: #282c34;--color-primary-light: wheat;--color-accent: #ff5722;--color-neutral: #c9c9c9;--color-fancy: #8b5cf6;--color-fancy-dark: #5d36b9;--color-success: #4caf50;--color-success-dark: #2d672f;--color-danger: #ef4444;--color-danger-dark: #ad2121}@tailwind base;@tailwind components;@tailwind utilities;body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#root{min-height:100vh;text-align:center;font-size:calc(10px + 2vmin);color:var(--color-primary);background-image:linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1)}div,span,button,label{user-select:none;-moz-user-select:none;-webkit-user-select:none}.container{max-width:1280px;margin:0 auto;padding:0.5rem}.logo{filter:hue-rotate(90deg);animation:hue 10s ease-in-out alternate infinite}@media (prefers-reduced-motion: reduce){.logo{animation:none}}@keyframes hue{from{filter:hue-rotate(90deg)}to{filter:hue-rotate(270deg)}}.link{display:flex;align-items:center;justify-content:center;gap:5px;color:var(--color-primary);fill:var(--color-primary)}.link:hover{color:var(--color-fancy);fill:var(--color-fancy)}.hamburger{display:grid;grid-template:auto 1fr auto / 1fr;row-gap:1rem;min-height:inherit}.switch{display:flex;align-items:center;justify-content:center;gap:5px}.switch__input{height:0;width:0;visibility:hidden}.switch__input:checked+.switch__fake-label{background:var(--color-accent)}.switch__input:checked+.switch__fake-label:after{left:calc(90%);transform:translateX(-100%)}.switch__fake-label{cursor:pointer;text-indent:-9999px;width:40px;height:21px;background:var(--color-neutral);display:block;border-radius:100px;position:relative}.switch__fake-label:after{content:"";position:absolute;top:1px;left:10%;width:18px;height:18px;background:var(--color-primary-light);border-radius:50%;transition:0.3s}.switch__fake-label:active:after{width:22px}.btn{display:flex;align-items:baseline;justify-content:center;gap:5px;color:var(--color-primary);fill:var(--color-primary);font-size:1.25rem;padding:0.5em 1em;font-weight:bold;border-radius:8px;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15)}.btn_success{background-color:var(--color-success)}.btn_success:hover{background-color:var(--color-success-dark);color:var(--color-primary-light);fill:var(--color-primary-light)}.btn_danger{background-color:var(--color-danger)}.btn_danger:hover{background-color:var(--color-danger-dark);color:var(--color-primary-light);fill:var(--color-primary-light)}.btn_fancy{background-color:var(--color-fancy)}.btn_fancy:hover{background-color:var(--color-fancy-dark);color:var(--color-primary-light);fill:var(--color-primary-light)}.timer-picker{max-width:fit-content;margin:0 auto}.time-picker-cell__input{border-radius:5px;position:relative;font-size:4rem;max-width:calc(2ch + 2 * .5rem);padding:.5rem;background-color:transparent}.time-picker-cell__input:hover:not(:focus){outline:1px solid var(--color-primary)}.time-picker-cell__input::selection{background-color:var(--color-accent)}.time-picker-cell__input_fancy{color:var(--color-fancy-dark)}.time-picker-cell__input_fancy .time-picker-cell__arrow{fill:var(--color-fancy-dark)}@media screen and (min-width: 768px){.time-picker-cell__input{font-size:6rem}}@media screen and (min-width: 1024px){.time-picker-cell__input{font-size:7rem}}.time-picker-cell__arrow{border-radius:6px;font-size:1rem;padding:5px 0;fill:var(--color-primary);display:flex;align-items:center;justify-content:center}.time-picker-cell__arrow:hover{background-color:var(--color-primary);color:var(--color-primary-light);fill:var(--color-primary-light)}
